import {
  Card,
  CardImg,
  CardText,
  CardBody,
  CardTitle,
  CardSubtitle,
  CardGroup,
  Button,
  Row,
  Col,
} from "reactstrap";
import { useNavigate } from "react-router-dom";
import React, { useEffect, useState } from "react";
import { trainingPlan } from "../../api/user";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";
const TrainingPlan = () => {
  const [apiData, setApiData] = useState("");
  const [isError, setIsError] = useState("");
  const id = localStorage.getItem("id");
  useEffect(() => {
    async function callApi() {
      try {
        // const apiResult = await axios.get(
        //   "	https://localhost:7016/api/TrainingPlan"
        // );

        //   setApiData(apiResult.data);
        const response = await trainingPlan(id);
      console.log(response)
        setApiData(response);
      } catch (error) {
        setIsError(error.message);
      }
    }
    callApi();
  }, []);

  const navigate = useNavigate();
  function ShowPage(blg) {
    navigate("../Trainincourse", { state: blg });
  }
  return (
    <div style={{ marginTop: "60px" }}>
      {/* --------------------------------------------------------------------------------*/}
      {/* Card-1*/}
      {/* --------------------------------------------------------------------------------*/}
      <h4 className="mb-3 caliber-font">My Training Plans</h4>
      {isError !== "" && <h2>{isError}</h2>}
      <Row>
        {apiData &&
          apiData.map((blg, index) => (
            <Col sm="6" lg="6" xl="3" key={index}>
              <Card className="Card caliber-font">
                {/* <CardImg alt="Card image cap" src={blg.featuredImage} style={{minHeight:"180px"}} /> */}
                <LazyLoadImage
                    loading="lazy"
                    src={blg.featuredImage}
                    effect="blur"
                    placeholderSrc={blg.featuredImage}
                    style={{
                      width: "100%", // Set the desired width
    height: "250px", // Set the desired height
    objectFit: "cover",
                    }}/>
                <CardBody className="p-2" style={{ height:"100%" }}>
                  <CardTitle className="Card-title" tag="h5">{blg.trainingPlanName}</CardTitle>
                  {/* <CardSubtitle>{blg.subtitle}</CardSubtitle> */}
                  <CardSubtitle>{blg.description.length > 40 ? blg.description.slice(0, 40) + '...' : blg.description}</CardSubtitle>
                  <CardText className="mt-3">{blg.text}</CardText>
                  <div style={{ display: "flex", justifyContent: "center" }}>
                  <Button color={blg.color} onClick={() => ShowPage(blg)}>
                    Enroll
                  </Button>
                  </div>
                </CardBody>
              </Card>
            </Col>
          ))}
      </Row>
    </div>
  );
};

export default TrainingPlan;
